import React, { Component } from "react";
import { Link } from "react-router-dom";

class Header extends Component {
  render() {
    return (
      <nav
        className="menu-classic menu-fixed menu-transparent menu-one-page align-right light"
        data-menu-anima="fade-bottom"
        data-scroll-detect="true"
      >
        <div className="container">
          <div className="menu-brand">
            <Link to="">
              <img
                className="logo-default"
                src={process.env.PUBLIC_URL + "images/logo.png"}
                alt="logo"
              />
              <img
                className="logo-retina"
                src={process.env.PUBLIC_URL + "images/logo.png"}
                alt="logo"
              />
            </Link>
          </div>
          <i className="menu-btn"></i>
          <div className="menu-cnt">
            <ul>
              <li>
                <Link to="/">Home</Link>
              </li>
              <li>
                <Link to="about-us">About</Link>
              </li>
              <li>
                <Link to="pricing">Pricing</Link>
              </li>
              <li className="dropdown">
                <Link to="services">Services</Link>
                <ul>
                  <li>
                    <Link to="success-stories">Services One</Link>
                  </li>
                  <li>
                    <Link to="success-stories">Services Two</Link>
                  </li>
                  <li>
                    <Link to="success-stories">Success stories</Link>
                  </li>
                </ul>
              </li>
              <li>
                <Link to="contact">Contact Us</Link>
              </li>
              <li>
                <Link to="blog">Blog</Link>
              </li>
            </ul>
            <div className="menu-right">
              <div className="menu-custom-area">
                <Link className="btn btn-xs btn-circle btn-border" to="#">
                  Sign up
                </Link>
              </div>
            </div>
            <div className="clear"></div>
          </div>
        </div>
      </nav>
    );
  }
}

export default Header;
